'use strict';

var elements = require('./elementsAndStableIsotopes.js');

var _require = require('./constants'),
    ELECTRON_MASS = _require.ELECTRON_MASS;

function getElementsObject() {
  var object = {};
  elements.forEach(function (e) {
    object[e.symbol] = e;
  });
  return object;
}

module.exports = {
  elements: elements,
  getElementsObject: getElementsObject,
  ELECTRON_MASS: ELECTRON_MASS
};